@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);

.logon-container{
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}
.logon-container section.form{
    width: 100%;
    max-width: 350;
    margin-right: 30px;
}
.logon-container section.form form {
    margin-top: 100px;
}
.logon-container section.form form h1{
    font-size: 32px;
    margin-bottom: 32px;
}
.logon-container section.form form a{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    color: #414141;
    font-size: 20px;
    text-decoration: none;
    font-weight: bold;
    transition: opacity 0.2s;
}
.logon-container section.form form a:hover{
    opacity: 0.8;
}
.i {
    margin-right: 10px;
}
img.imgLogo {
    height: 250px;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}
body{
    font : 400 14px Roboto,sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}
input, button, textarea ,select{
    font : 400 18px Roboto,sans-serif;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
button{
    cursor: pointer;
}
form textarea{
    width: 100%;
    resize: vertical;
    min-height: 140px;
    color:#333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding : 16px 24px;
    line-height: 24px;
}
form input,select
 {
    width: 100%;
    height: 60px;
    color:#333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding : 0 24px;
}
.button{
    width : 100%;
    height: 60px;
    background: #e061a7;
    border: 0;
    border-radius: 8px;
    color: #FFF;
    font-weight:700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}
.button:hover {
    -webkit-filter:brightness(80%);
            filter:brightness(80%)
}
.back-link {
    display: flex;
    align-items: center;
    margin-top: 40px;
    color : #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}
.back-link svg{
    margin-right: 8px ;
}   
.back-link a:hover {
    opacity: 0.8;
}
