.dropzone {
    height: 200px;
    background: #ffff;
    border-radius: 10px;
    border: 1px solid #dcdce6;
  
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    outline: 0;
  }
  
  .dropzone img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .dropzone p {
    width: calc(100% - 60px);
    height: calc(100% - 60px);
    border-radius: 10px;
    border: 1px dashed black;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #333;
  }
  
  .dropzone p svg {
    color: white;
    width: 24px;
    height: 24px;
    margin-bottom: 8px;
  }
